import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";

/**
 * Class that defines input for cart operations.
 * @extends DigitalCommerceInput
 */
export class AddToCartInput extends DigitalCommerceInput {
  /**
   * catalogCode to be used in part of the path of the API.
   */
  public catalogCode: string;

  /**
   *  Persisting cartContext hash key to be used in part of the path of the API to maintain existing cart.  For fresh cart, no need to pass this.
   */
  public cartContextKey?: string;

  /**
   * A required POST Body param to provide one of the three possible actions for the underlying API:
   * AddWithNoConfig - if adding directly from offerCode; requires POST Body param 'offer'.
   * AddAfterConfig - if adding from configured offerDetails; requires POST Body param 'productConfig'
   * DeleteFromBasket - if deleting a bundle; srequires POST Body param 'deleteBundleNumber'
   */
  public basketAction: string;

  /**
   * An optional POST Body param to specify offer code to be added to cart; only needed if offer is being added without any configuration, in which case basketAction is "AddWithNoConfig".
   */
  public offerCode?: string;

  /**
   * An optional POST Body param to provide configured offer details taken from response of configure API to add configured offer to cart, in which case basketAction is "AddAfterConfig".
   * This can be provided as a Map (direct response of configure API) or as a stringified version of the Map.
   */
  public offerConfiguration?: any;

  /**
   * context param as part of the query string of the API to define which offers qualify in returning result set.
   */
  public context?: string;

  /**
   * forceInvalidateCache as part of the query string of the API to refresh the cache for a particular catalog.
   */
  public forceInvalidateCache?: boolean;

  /**
   * An optional POST Body object to store offer into cart for the further api requests.
   */
  public transactionKey?: string;

  /**
   * An optional POST Body parameter for adding items to cart.
   */
  public multiTransactionKey?: string;

  /**
   * An optional POST Body parameter for fetching the attachments.
   */
  public includeAttachment?: boolean = false;

  /**
   * validatePrice is part of the query string of the API todo price validation check.
   */
  public validatePrice?: boolean = true;

  /**
   * validateAction is part of the query string of the API todo context rule validation check.
   */
  public validateAction?: boolean = true;

  /**
   * An POST Body object for the api requests.
   */
  public actionParams?: any;

  /**
   * isLoggedIn as part of the query string of the API.
   */
  public isLoggedIn?: boolean;

  /**
   * optional input param referencedatetime as part of the query string of the API. Should be valid date time example: 2021-07-23T12:00:000z
   */
  public referencedatetime?: string;

  /**
   * Constructor function for GetCatalogOffersInput class
   */
  constructor() {
    super();
    this.remoteMethod = "";
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (this.context) {
      inputMap.context = this.context;
    }

    if (this.forceInvalidateCache) {
      inputMap.forceinvalidatecache = this.forceInvalidateCache;
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    if (this.includeAttachment) {
      inputMap.includeAttachment = this.includeAttachment;
    }

    if (this.isLoggedIn) {
      inputMap.isloggedin = this.isLoggedIn;
    }

    if (!this.validatePrice) {
      inputMap.price = false;
    }
    if (!this.validateAction) {
      inputMap.validate = false;
    }

    if (this.referencedatetime) {
      inputMap.referencedatetime = this.referencedatetime;
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    const encodedQueryString = this.getEncodedQueryString();
    const cartContextKey = this.cartContextKey || "";
    const cartContextKeySeparator = cartContextKey !== "" ? "/" : "";

    if (!this.catalogCode) {
      throw new Error("AddToCartInput::getAPIPath() must have catalogCode.");
    }

    return (
      `${SalesForceNamespace.restAPIPrefix}/v3/catalogs/${
        this.catalogCode
      }/basket${cartContextKeySeparator}${cartContextKey}` + encodedQueryString
    );
  }

  /**
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    const encodedQueryString = this.getEncodedQueryString();
    const cartContextKey = this.cartContextKey || "";
    const cartContextKeySeparator = cartContextKey !== "" ? "/" : "";

    if (!DigitalCommerceSDKConfig.anonymousSiteConfigUrl) {
      throw new Error(
        "AddToCartInput::getAnonymousRestUrl() must have anonymousSiteUrl."
      );
    }

    if (!this.catalogCode) {
      throw new Error(
        "AddToCartInput::getAnonymousSiteRestUrl() must have catalogCode."
      );
    }

    return (
      DigitalCommerceSDKConfig.anonymousSiteConfigUrl +
      `/v3/catalogs/${
        this.catalogCode
      }/basket${cartContextKeySeparator}${cartContextKey}` +
      encodedQueryString
    );
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    if (!this.secureServerEndPointUrl) {
      this.secureServerEndPointUrl = "/addtocart";
    }
    return (
      DigitalCommerceSDKConfig.secureServerUrl + this.secureServerEndPointUrl
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest and Rest
   * @returns {string}
   */

  public getRequestPayload(): any {
    if (!this.basketAction) {
      throw new Error(
        "AddToCartInput::getRequestPayload() must have basketAction."
      );
    }

    if (
      this.basketAction &&
      this.basketAction === "AddWithNoConfig" &&
      !this.offerCode
    ) {
      throw new Error(
        "AddToCartInput::getRequestPayload() must have offerCode when basketAction is 'AddWithNoConfig'."
      );
    }

    if (
      this.basketAction &&
      this.basketAction === "AddAfterConfig" &&
      !this.offerConfiguration
    ) {
      throw new Error(
        "AddToCartInput::getRequestPayload() must have offerConfiguration when basketAction is 'AddAfterConfig'."
      );
    }
    if (this.basketAction && this.basketAction === "DeleteFromBasket") {
      if (!this.actionParams.bundleContextKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have bundleContextKey when basketAction is 'DeleteFromBasket'."
        );
      }
      if (!this.actionParams.lineItemKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have lineItemKey when basketAction is 'DeleteFromBasket'."
        );
      }
    }

    if (this.basketAction && this.basketAction === "AddChildProduct") {
      if (!this.actionParams.bundleContextKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have bundleContextKey when basketAction is 'AddChildProduct'."
        );
      }
      if (!this.actionParams.parentLineItemKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have parentLineItemKey when basketAction is 'AddChildProduct'."
        );
      }
      if (!this.actionParams.parentHierarchyPath) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have parentHierarchyPath when basketAction is 'AddChildProduct'."
        );
      }
      if (!this.actionParams.offer) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have offer when basketAction is 'AddChildProduct'."
        );
      }
    }

    if (this.basketAction && this.basketAction === "DeleteChildProduct") {
      if (!this.actionParams.bundleContextKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have bundleContextKey when basketAction is 'DeleteChildProduct'."
        );
      }
      if (!this.actionParams.lineItemKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have lineItemKey when basketAction is 'DeleteChildProduct'."
        );
      }
    }
    if (this.basketAction && this.basketAction === "UpdateChildProduct") {
      if (!this.actionParams.bundleContextKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have bundleContextKey when basketAction is 'UpdateChildProduct'."
        );
      }
      if (!this.actionParams.lineItemKey) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have lineItemKey when basketAction is 'UpdateChildProduct'."
        );
      }
    }
    if (this.basketAction && this.basketAction === "DeletePromotion") {
      if (!this.actionParams.offer) {
        throw new Error(
          "AddToCartInput::getRequestPayload() must have offerCode when basketAction is 'DeletePromotion'."
        );
      }
    }
    if (
      this.basketAction &&
      this.basketAction === "AddPromotion" &&
      !this.offerCode
    ) {
      throw new Error(
        "AddToCartInput::getRequestPayload() must have offerCode when basketAction is 'AddPromotion'."
      );
    }

    let requestBody: any = {};
    requestBody.basketAction = this.basketAction;
    if (this.basketAction === "AddWithNoConfig") {
      requestBody.offer = this.offerCode;
    } else if (this.basketAction === "AddAfterConfig") {
      requestBody.productConfig = this.offerConfiguration;
    } else if (this.basketAction === "DeleteFromBasket") {
      requestBody = Object.assign(requestBody, this.actionParams);
    } else if (this.basketAction === "AddChildProduct") {
      requestBody.offer = this.offerCode;
      requestBody.basketAction = "AddWithNoConfig";
      requestBody = Object.assign(requestBody, this.actionParams);
    } else if (this.basketAction === "DeleteChildProduct") {
      requestBody.basketAction = "DeleteFromBasket";
      requestBody = Object.assign(requestBody, this.actionParams);
    } else if (this.basketAction === "DeletePromotion") {
      requestBody.basketAction = "DeleteFromBasket";
      requestBody = Object.assign(requestBody, this.actionParams);
    } else if (this.basketAction === "AddPromotion") {
      requestBody.basketAction = "AddWithNoConfig";
      requestBody.offer = this.offerCode;
      requestBody = Object.assign(requestBody, this.actionParams);
    }
    if (this.transactionKey) {
      requestBody.transactionKey = this.transactionKey;
    }
    if (this.multiTransactionKey) {
      requestBody.multiTransactionKey = this.multiTransactionKey;
    }

    return requestBody;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [
      "forceinvalidatecache",
      "fields",
      "context",
      "includeAttachment",
      "price",
      "validate",
      "isloggedin",
      "referencedatetime"
    ];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
