import DigitalCommerce from "../digitalcommerce.sdk";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";
import { Action } from "./Action";

/**
 * Base class that defines an input
 */
export abstract class DigitalCommerceInput {
  /**
   * anonymousSiteUrl as the prefix of the API url for Off-Platform deployments.
   */
  public anonymousSiteUrl: string;

  /**
   * API namespace.
   */
  public apiNamespace: string;

  /**
   * Remote class that handles this request.
   */
  public remoteClass: string = "CpqAppHandler";

  /**
   * Remote method that handles this request.
   */
  public remoteMethod: string;

  /**
   * List of fields to be returned.
   */
  public fields?: string[];

  /**
   * List of custom fields to be returned.
   */
  public customFields?: string[] = [];

  /**
   * Flag to indicate if the sdk call should be cached.
   */
  public cacheable?: boolean = false;

  /**
   * Flag to indicate where the data to be cached.
   * Supported types are "INDEXEDDB, SESSIONSTORAGE and LOCALSTORAGE"
   */
  public cacheType?: string;

  /**
   * Expiration Date time of the cached item.
   * Default is current time plus 5 minutes.
   */
  public cacheExpiration?: Date;

  /**
   * boolean value to decide if secure server needs to be called.
   */
  public callSecureServer?: boolean = false;

  /**
   * Boolean flag to discard cache and call the api again.
   * Default is false.
   */
  public reload?: boolean = false;

  /**
   * Flag to indicate if the sdk call should be cached.
   */
  public timeoutInMillis?: number = 20000;

  /**
   * Action object json.
   */
  public actionObj: Action;

  /**
   * integration procedure name - optional
   */
  public integrationProcedureKey?: string = "";

  /**
   * User accountId for checkout api calls - optional
   */
  public accountId?: string = "";

  /**
   * Options Map values for the integration procedure.
   */
  public optionsMap?: any;

  /*
   * content type of the resource.
   */
  public contentType?: string = "application/json";

  /**
   * value indicating whether the user agent should send cookies from the other domain in the case of cross-origin requests
   */
  public requestCredentials?: boolean;

  /**
   * optional input param used as secure Server rest URL end point when Proxy SDK is used
   */
  public secureServerEndPointUrl?: string;

  /**
   * execute Integration Procedure as chainable.
   */
  public chainable?: boolean;

  /**
   * Input Map values for the dataraptor procedure.
   */
  public inputMap?: any = {};

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public abstract getInputMap(): any;

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public abstract getAPIPath(): string;

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public abstract getSecureServerRestUrl(): string;

  /**
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public abstract getAnonymousSiteRestUrl(): string;

  /**
   * Function to create request body for API POST.
   * @returns {any}
   */
  public abstract getRequestPayload(): any;

  /**
   * Function to return VF Remoting method name.
   * @returns {string}
   */
  public getRemoteMethod(): string {
    if (this.actionObj) {
      return this.actionObj.remote.methodName;
    } else {
      return this.remoteMethod;
    }
  }

  /**
   * Function to return authorization token
   * @param {string} context - optional param.
   * @returns {string}
   */
  public async getAuthToken(context?: DigitalCommerce): Promise<string> {
    if (DigitalCommerceSDKConfig.authToken) {
      if (
        DigitalCommerceSDKConfig.authTokenExpirationTime &&
        DigitalCommerceSDKConfig.authTokenExpirationTime < Date.now()
      ) {
        await context.fetchAWSAuthToken();
      }
      return DigitalCommerceSDKConfig.authToken;
    } else {
      return "";
    }
  }

  /**
   * content type of the resource.
   */
  public getAuthorisationType(): string {
    if (DigitalCommerceSDKConfig.authTokenType) {
      return DigitalCommerceSDKConfig.authTokenType;
    } else {
      return "Basic ";
    }
  }

  /**
   * The credentials read-only property of the Request interface indicates whether the user agent should send cookies from the other domain in the case of cross-origin requests.
   * @returns {string}
   */
  public getRequestCredentials(): RequestCredentials {
    return null;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected abstract getEncodedQueryString(): string;
}
