import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";

/**
 * Class that defines input to create an basket from an asset operations.
 * @extends DigitalCommerceInput
 */
export class AssetToBasketInput extends DigitalCommerceInput {
  /**
   * catalogCode to be used in part of the path of the API.
   */
  public catalogCode: string;

  /**
   * A required POST Body param that the action to be performed:
   * AssetToBasket - while passing list of root asset ids to be converted to basket, ; requires input param 'accountId'.
   */
  public basketAction: string;

  /**
   * isLoggedIn as part of the query string of the API.
   * By default this isLoggedIn value is set to true.
   */
  public isLoggedIn: boolean = true;

  /**
   * canCreateBasket as part of the query string of the API.
   * By default its false, when canCreateBasket=true is passed, it includes the canCreateFDO step in case of cacheMiss.
   */
  public canCreateBasket?: boolean;

  /**
   * context param as part of the query string of the API to define which offers qualify in returning result set.
   * Since this api is for known user only, hence context is a required parameter. accountId is mandatory context parameter
   */
  public context: any;

  /**
   * forceInvalidateCache as part of the query string of the API to refresh the cache for the basket response.
   */
  public forceInvalidateCache?: boolean;

  /**
   * An optional POST Body parameter for adding items to cart.
   */
  public multiTransactionKey?: string;

  /**
   * An optional POST Body object to store offer into cart for the further api requests.
   */
  public transactionKey?: string;

  /**
   * validatePrice is part of the query string of the API.
   * optional parameter (By default its true, when price=false is passed, the pricing is not run on the orderitems)
   */
  public validatePrice?: boolean = true;

  /**
   * validateAction is part of the query string of the API.
   * optional parameter (By default its true, when validateAction=false is passed, the validation rules are not run on the orderitems)
   */
  public validateAction?: boolean = true;

  /**
   * POST Body parameter for to create an basket from an asset.
   * list of root asset ids to be converted to basket, forex:"02i4P000009ion5QAA,02i4P000009ion6QAA"
   */
  public rootAssetIds: any;

  /**
   * POST Body parameter for to create an basket from an asset.
   * date or date time in UTC format
   */
  public requestDateTime: string;

  /**
   * Constructor function for AssetToBasketInput class
   */
  constructor() {
    super();
    this.remoteMethod = "";
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (this.context) {
      inputMap.context = this.context;
    }

    if (this.forceInvalidateCache) {
      inputMap.forceinvalidatecache = this.forceInvalidateCache;
    }

    if (this.canCreateBasket) {
      inputMap.canCreateBasket = this.canCreateBasket;
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    if (!this.validatePrice) {
      inputMap.price = false;
    }

    if (!this.validateAction) {
      inputMap.validate = false;
    }

    if (this.isLoggedIn) {
      inputMap.isloggedin = true;
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!this.catalogCode) {
      throw new Error(
        "AssetToBasketInput::getAPIPath() must have catalogCode."
      );
    }

    if (!this.context) {
      throw new Error("AssetToBasketInput::getAPIPath() must have context.");
    }

    if (!this.isLoggedIn) {
      throw new Error(
        "AssetToBasketInput::getAPIPath() must have set isLoggedIn=true."
      );
    }

    return (
      `${SalesForceNamespace.restAPIPrefix}/v3/catalogs/${
        this.catalogCode
      }/basket` + encodedQueryString
    );
  }

  /**
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    return "";
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    if (!this.secureServerEndPointUrl) {
      this.secureServerEndPointUrl = "/assettobasket";
    }
    return (
      DigitalCommerceSDKConfig.secureServerUrl + this.secureServerEndPointUrl
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * @returns {string}
   */

  public getRequestPayload(): any {
    if (!this.rootAssetIds) {
      throw new Error(
        "AssetToBasketInput::getRequestPayload() must have rootAssetIds."
      );
    }

    if (!this.basketAction) {
      throw new Error(
        "AssetToBasketInput::getRequestPayload() must have basketAction."
      );
    }

    if (!this.requestDateTime) {
      throw new Error(
        "AssetToBasketInput::getRequestPayload() must have valid requestDateTime input."
      );
    }

    const requestBody: any = Object.assign(
      {},
      {
        basketAction: this.basketAction,
        requestDateTime: this.requestDateTime,
        rootAssetIds: this.rootAssetIds
      }
    );

    if (this.multiTransactionKey) {
      requestBody.multiTransactionKey = this.multiTransactionKey;
    }

    if (this.transactionKey) {
      requestBody.transactionKey = this.transactionKey;
    }

    return requestBody;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [
      "forceinvalidatecache",
      "fields",
      "context",
      "price",
      "validate",
      "canCreateBasket",
      "isloggedin"
    ];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
