import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";
/**
 * Class that defines input to authenticate user.
 * @extends DigitalCommerceInput
 */

export class AuthenticateUserInput extends DigitalCommerceInput {
  /**
   * email as part of the POST body of the API for authentication.
   */
  public email: string;

  /**
   * User session token.
   */
  public sessionToken: any;

  /**
   * phoneNumber as part of the POST body of the API for authentication.
   */
  public phoneNumber?: string;

  /**
   * firstName as part of the POST body of the API for authentication.
   */
  public firstName?: string;

  /**
   * firstName as part of the POST body of the API for authentication.
   */
  public lastName?: string;

  /**
   * boolean value to decide if secure server needs to be called.
   */
  public callSecureServer?: boolean = true;

  /**
   * Constructor function for AuthenticateUserInput class
   */
  constructor() {
    super();
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    return "";
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(): any {
    if (!this.email) {
      throw new Error(
        "AuthenticateUserInput::getRequestPayload() must have email."
      );
    }

    if (!this.sessionToken) {
      throw new Error(
        "AuthenticateUserInput::getRequestPayload() must have sessionToken."
      );
    }

    if (!this.firstName) {
      throw new Error(
        "AuthenticateUserInput::getRequestPayload() must have firstName."
      );
    }

    if (!this.lastName) {
      throw new Error(
        "AuthenticateUserInput::getRequestPayload() must have lastName."
      );
    }

    if (!this.phoneNumber) {
      throw new Error(
        "AuthenticateUserInput::getRequestPayload() must have phoneNumber."
      );
    }

    let requestBody: any = {
      email: this.email,
      firstName: this.firstName,
      lastName: this.lastName,
      phoneNumber: this.phoneNumber,
      sessionToken: this.sessionToken
    };

    if (this.integrationProcedureKey !== "") {
      requestBody = {
        AccountEmail: this.email,
        AccountName: this.email,
        AccountRecordType: "Billing",
        AccountStatus: "Active"
      };
    }
    return requestBody;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    return inputMap;
  }

  /**
   * Function that constructs the node server SDK API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    if (!DigitalCommerceSDKConfig.secureServerUrl) {
      throw new Error(
        "AuthenticateUserInput::getAnonymousRestUrl() must have secureServerUrl."
      );
    }

    const encodedQueryString = this.getEncodedQueryString();
    return (
      DigitalCommerceSDKConfig.secureServerUrl + `/signin` + encodedQueryString
    );
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    return this.getAnonymousSiteRestUrl();
  }

  /**
   * The credentials read-only property of the Request interface indicates whether the user agent should send cookies from the other domain in the case of cross-origin requests.
   * @returns {string}
   */
  public getRequestCredentials(): RequestCredentials {
    if (DigitalCommerceSDKConfig.secureServerRequestCredentials) {
      return DigitalCommerceSDKConfig.secureServerRequestCredentials;
    }
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
