import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";
/**
 * Class that defines input to create and submit the order.
 * @extends DigitalCommerceInput
 */
export class SubmitOrderInput extends DigitalCommerceInput {
  /**
   * catalogCode as part of the POST body of the API for submit the order.
   */
  public catalogCode?: string;

  /**
   * cartContextKey as part of the POST body of the API for submit the order.
   */
  public cartContextKey?: string;

  /**
   * orderId as part of the POST body of the API for submit the order.
   */
  public orderId?: string;

  /**
   * boolean value to decide if secure server needs to be called.
   */
  public callSecureServer?: boolean = true;

  /**
   * Constructor function for SubmitOrderInput class
   */
  constructor() {
    super();
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {string}
   */
  public getAPIPath(): string {
    return "";
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    if (!this.secureServerEndPointUrl) {
      this.secureServerEndPointUrl = "/submitorder";
    }

    return (
      DigitalCommerceSDKConfig.secureServerUrl + this.secureServerEndPointUrl
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when input parameters are not provided
   * @returns {any}
   */
  public getRequestPayload(): any {
    if (!this.catalogCode && !this.orderId) {
      throw new Error(
        "SubmitOrderInput::getRequestPayload() must have catalogCode."
      );
    }
    if (!this.cartContextKey && !this.orderId) {
      throw new Error(
        "SubmitOrderInput::getRequestPayload() must have cartContextKey."
      );
    }

    const requestBody: any = {
      cartContextKey: this.cartContextKey,
      catalogCode: this.catalogCode
    };
    if (this.orderId) {
      requestBody.orderId = this.orderId;
    }
    if (this.integrationProcedureKey !== "") {
      requestBody.accountId = this.accountId;
    }
    return requestBody;
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};
    return inputMap;
  }

  /**
   * Function that constructs the node server SDK API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    if (!DigitalCommerceSDKConfig.secureServerUrl) {
      throw new Error(
        "SubmitOrderInput::getAnonymousRestUrl() must have secureServerUrl."
      );
    }

    const encodedQueryString = this.getEncodedQueryString();
    return (
      DigitalCommerceSDKConfig.secureServerUrl +
      `/submitorder` +
      encodedQueryString
    );
  }
  /**
   * The credentials read-only property of the Request interface indicates whether the user agent should send cookies from the other domain in the case of cross-origin requests.
   * @returns {string}
   */
  public getRequestCredentials(): RequestCredentials {
    if (DigitalCommerceSDKConfig.secureServerRequestCredentials) {
      return DigitalCommerceSDKConfig.secureServerRequestCredentials;
    }
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
