import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";

/**
 * Class that defines input for getting product and promotions associated with a catalogCode.
 * @extends DigitalCommerceInput
 */
export class GetOffersInput extends DigitalCommerceInput {
  /**
   * catalogCode to be used in part of the query string of the API to get associated products and promotions.
   */
  public catalogCode: string;

  /**
   * context params as part of the query string of the API to define which offers qualify in returning result set.
   */
  public context?: string;

  /**
   * offset as part of the query string of the API for pagination.
   */
  public offset?: number;

  /**
   * pageSize as part of the query string of the API for pagination.
   */
  public pageSize?: number;

  /**
   * contains as part of the query string of the API to get related offers.
   */
  public contains?: string;

  /**
   * forceInvalidateCache as part of the query string of the API to refresh the cache for a particular catalog.
   */
  public forceInvalidateCache?: boolean;

  /**
   * sortBy as part of the query string of the API to specify sorting field.
   */
  public sortBy?: string;

  /**
   * isLoggedIn as part of the query string of the API.
   */
  public isLoggedIn?: boolean;

  /**
   * includeExtraOfferAttributes as part of the query string of the API which includes extra passed attributes with default attributes in Offer
   */
  public includeAttributes?: string[] = [];
  /**
   * optional input param used only for getFilteredOffersList call. If true return only promotions, If false return only products
   */
  public returnPromotion?: boolean;
  /**
   * optional input param used only for getPromotions call.
   */
  public offerCode?: string;

  /**
   * This parameter takes in a list of offer codes ( comma separated string), and returns a list of eligible offers from the input list.
   * eg: input.offerList = "iphone4,iphone5";
   */
  public offerList?: string;

  /**
   * optional input param used for retrieval of Cost details
   */
  public costDetailsRequired?: boolean;

  /**
   * optional input param referencedatetime as part of the query string of the API. Should be valid date time example: 2021-07-23T12:00:000z
   */
  public referencedatetime?: string;

  /**
   * Constructor function for GetOffersInput class
   */
  constructor() {
    super();
    this.remoteMethod = "";
    this.contains = "";
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (this.context) {
      inputMap.context = this.context;
    }

    if (this.offset || this.offset >= 0) {
      inputMap.offset = this.offset;
    }

    if (this.pageSize) {
      inputMap.pagesize = this.pageSize;
    }

    if (this.contains) {
      inputMap.contains = this.contains;
    }

    if (this.forceInvalidateCache) {
      inputMap.forceinvalidatecache = this.forceInvalidateCache;
    }

    if (this.sortBy) {
      inputMap.sortby = this.sortBy;
    }

    if (this.isLoggedIn) {
      inputMap.isloggedin = this.isLoggedIn;
    }

    if (this.referencedatetime) {
      inputMap.referencedatetime = this.referencedatetime;
    }

    if (this.offerList) {
      if (typeof this.offerList === "string") {
        inputMap.offerList = this.offerList.trim();
      } else {
        throw new Error("GetOffersInput:  offerList must be a string.");
      }
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    return inputMap;
  }

  public isValidInput(): boolean {
    if (!this.catalogCode) {
      throw new Error("GetOffersInput::getAPIPath() must have catalogCode.");
    }
    return true;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    if (!this.catalogCode) {
      throw new Error("GetOffersInput::getAPIPath() must have catalogCode.");
    }

    if (this.contains && this.sortBy) {
      throw new Error(
        "GetCatalogOffersContainsInput::getAPIPath() should not have both sortBy and contains."
      );
    }

    return (
      `${SalesForceNamespace.restAPIPrefix}/v3/catalogs/${
        this.catalogCode
      }/offers` + this.getEncodedQueryString()
    );
  }

  /**
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!this.catalogCode) {
      throw new Error(
        "GetOffersInput::getAnonymousRestUrl() must have catalogCode."
      );
    }

    if (!DigitalCommerceSDKConfig.anonymousSiteConfigUrl) {
      throw new Error(
        "GetOffersInput::getAnonymousRestUrl() must have anonymousSiteUrl."
      );
    }

    if (this.contains && this.sortBy) {
      throw new Error(
        "GetOffersInput::getAnonymousRestUrl() should not have both sortBy and contains."
      );
    }

    return (
      DigitalCommerceSDKConfig.anonymousSiteConfigUrl +
      `/v3/catalogs/${this.catalogCode}/offers` +
      encodedQueryString
    );
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    if (!this.secureServerEndPointUrl) {
      this.secureServerEndPointUrl = "/getoffers";
    }
    return (
      DigitalCommerceSDKConfig.secureServerUrl + this.secureServerEndPointUrl
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when JSONResult, accountId and catalogCode is null
   * @returns {string}
   */
  public getRequestPayload(): string {
    return "";
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [
      "offset",
      "pagesize",
      "forceinvalidatecache",
      "contains",
      "fields",
      "sortby",
      "isloggedin",
      "context",
      "offerList",
      "referencedatetime"
    ];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
