import { BaseSDK } from "../common";
import { Logger } from "../logger";

/**
 * A basic pub-sub mechanism for sibling component communication.
 * @example getting an instance of PubSub
 * ```typescript
 * // Obtain a singleton Pub Sub service
 * const pubSubService = PubSub.getInstance();
 * ```
 * @example registering an event
 * ```typescript
 * pubSubService.register('click',{
 * 'click': (function() {alert("clicked")})
 * })
 * ```
 * @example unregistering the event
 * ```typescript
 * pubSubService.unregister('click',{
 * 'click': (function() {alert("clicked")})
 * })
 * ```
 * @example firing the event
 * ```typescript
 * pubSubService.fire('click', 'click', data, 1000)
 * ```
 */
export default class PubSub extends BaseSDK {
  /**
   * Get a singleton instance of this PubSub.
   * @returns an instance of this SDK.
   */
  public static getInstance(): PubSub {
    if (!PubSub.instance) {
      PubSub.instance = new PubSub();
    }
    return PubSub.instance;
  }
  /**
   * The instance of this SDK
   */
  private static instance: PubSub;
  /**
   * Stores a callback for each event
   */
  private callbacks = new Map<string, Set<string>>();

  constructor() {
    super();
    if (PubSub.instance) {
      throw new Error(
        `Existing PubSub instance. Don't create instances, use the singleton from PubSub.instance`
      );
    }
  }
  /**
   * Registers a callback for an event
   * @param {string} eventName - Name of the event to listen for.
   * @param {object} callbackobj - Contains callback function.
   */
  public register(eventName, callbackobj) {
    if (!this.callbacks[eventName]) {
      this.callbacks[eventName] = new Set();
    }
    this.callbacks[eventName].add(callbackobj);
  }

  /**
   * Unregisters a callback for an event
   * @param {string} eventName - Name of the event to unregister from.
   * @param {object} callbackobj - Contains callback function, it should refer to same object which is used to register the event.
   */
  public unregister(eventName, callbackobj) {
    if (this.callbacks[eventName]) {
      this.callbacks[eventName].delete(callbackobj);
    }
  }

  /**
   * Fires an event to listeners.
   * @param {string} eventName - Name of the event to fire.
   * @param {string} action - Name of the event to fire.
   * @param {*} payload - Payload of the event to fire.
   */
  public fire(eventName, action, payload) {
    if (this.callbacks[eventName]) {
      this.callbacks[eventName].forEach(callback => {
        try {
          callback[action] && callback[action](payload);
        } catch (error) {
          Logger.error(
            `Callback for ${eventName} and ${action} failed. There is an error in the registered callback: `,
            error
          );
        }
      });
    }
  }
}
