import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";

/**
 * Class that defines input for getting offer details  .
 * @extends DigitalCommerceInput
 */
export class GetOfferInput extends DigitalCommerceInput {
  /**
   * catalogCode to be used in part of the path of the API to get associated products and promotions.
   */
  public catalogCode: string;

  /**
   * offerCode to be used in part of the path of the API to get the details of the associated product.
   */
  public offerCode: string;

  /**
   * contextKey as part of the query string of the API describes the anonymous or known customer's "context" to be used
   * in rule processing.
   */
  public contextKey?: string;

  /**
   * context params as part of the query string of the API to define which offers qualify in returning result set.
   */
  public context?: string;

  /**
   * contextDebug as part of the query string of the API for returning a full context name:value pair array. This
   * parameter only works when a server setting enables contextdebug to be set from the API.
   */
  public contextDebug?: boolean;

  /**
   * forceInvalidateCache as part of the query string of the API to refresh the cache for a particular catalog.
   */
  public forceInvalidateCache?: boolean;

  /**
   * include as part of the query string of the API for controlling what data about related products and
   * promotions is included in the response.
   */
  public include?: string;

  /**
   * quantity as part of the query string of the API for getting quantity of the offer desired.
   */
  public quantity?: number;

  /**
   * attributes as part of the query string of the API for getting Attribute values chosen for the desired offer.
   */
  public attributes?: string;

  /**
   * isLoggedIn as part of the query string of the API.
   */
  public isLoggedIn?: boolean;

  /**
   * optional input param used for retrieval of Cost details
   */
  public costDetailsRequired?: boolean;

  /**
   * optional input param referencedatetime as part of the query string of the API. Should be valid date time example: 2021-07-23T12:00:000z
   */
  public referencedatetime?: string;

  /**
   * Constructor function for GetCatalogOffersInput class
   */
  constructor() {
    super();
    this.remoteMethod = "";
  }

  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (this.actionObj) {
      return this.actionObj.remote.params || {};
    }

    if (this.contextKey) {
      inputMap.contextkey = this.contextKey;
    }

    if (this.context) {
      inputMap.context = this.context;
    }

    if (this.contextDebug) {
      inputMap.contextdebug = this.contextDebug;
    }

    if (this.forceInvalidateCache) {
      inputMap.forceinvalidatecache = this.forceInvalidateCache;
    }

    if (this.include) {
      inputMap.includerelated = this.include;
    }

    if (this.quantity) {
      inputMap.quantity = this.quantity;
    }

    if (this.isLoggedIn) {
      inputMap.isloggedin = this.isLoggedIn;
    }

    if (this.attributes) {
      inputMap.attributes = this.attributes;
    }

    if (this.referencedatetime) {
      inputMap.referencedatetime = this.referencedatetime;
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!this.actionObj && !this.catalogCode) {
      throw new Error("GetOfferInput::getAPIPath() must have catalogCode.");
    }

    if (!this.actionObj && !this.offerCode) {
      throw new Error("GetOfferInput::getAPIPath() must have offerCode.");
    }

    if (this.actionObj) {
      return (
        SalesForceNamespace.restAPIPrefix +
        this.actionObj.rest.link +
        encodedQueryString
      );
    } else {
      return `${SalesForceNamespace.restAPIPrefix}/v3/catalogs/${
        this.catalogCode
      }/offers/${this.offerCode}${encodedQueryString}`;
    }
  }

  /**
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!DigitalCommerceSDKConfig.anonymousSiteConfigUrl) {
      throw new Error(
        "GetOfferInput::getAnonymousRestUrl() must have anonymousSiteUrl."
      );
    }

    if (this.actionObj) {
      return (
        DigitalCommerceSDKConfig.anonymousSiteConfigUrl +
        this.actionObj.rest.link +
        encodedQueryString
      );
    } else {
      return `${DigitalCommerceSDKConfig.anonymousSiteConfigUrl}/v3/catalogs/${
        this.catalogCode
      }/offers/${this.offerCode}${encodedQueryString}`;
    }
  }
  /**
   * Function that constructs the Request body json for ApexRest and Rest when we are using HTTP methods of POST or PUT.
   * @returns {any}
   */
  public getRequestPayload(): any {
    return null;
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    if (!this.secureServerEndPointUrl) {
      this.secureServerEndPointUrl = "/getoffer";
    }
    return (
      DigitalCommerceSDKConfig.secureServerUrl + this.secureServerEndPointUrl
    );
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [
      "contextkey",
      "contextdebug",
      "forceinvalidatecache",
      "includerelated",
      "quantity",
      "attributes",
      "fields",
      "isloggedin",
      "context",
      "referencedatetime"
    ];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
