import { SalesForceNamespace } from "../../common";
import { queryStringEncode } from "../../utils";
import { DigitalCommerceInput } from "../common/DigitalCommerceInput";
import DigitalCommerceSDKConfig from "../DigitalCommerceSDKConfig";

/**
 * Class that defines input for validating a offer configuration.
 * @extends DigitalCommerceInput
 */
export class ValidateOfferInput extends DigitalCommerceInput {
  /**
   * catalogCode to be used in part of the path of the API to get associated products and promotions.
   */
  public catalogCode: string;

  /**
   * offerCode to be used in part of the path of the API to get the details of the associated product.
   */
  public offerCode: string;

  /**
   * context params as part of the query string of the API to define which offers qualify in returning result set.
   */
  public context?: string;

  /**
   * forceInvalidateCache as part of the query string of the API to refresh the cache for a particular catalog.
   */
  public forceInvalidateCache?: boolean;

  /**
   * offerConfiguration JSON of the offer to be use as request body of the API for configuration, to get the resultant price.
   */
  public offerConfiguration: any;

  /**
   * isLoggedIn as part of the query string of the API.
   */
  public isLoggedIn?: boolean;

  /**
   * optional input param referencedatetime as part of the query string of the API. Should be valid date time example: 2021-07-23T12:00:000z
   */
  public referencedatetime?: string;

  /**
   * Constructor function for GetCatalogOffersInput class.
   */
  constructor() {
    super();
    this.remoteMethod = "";
  }

  public isValidInput(): boolean {
    if (!this.offerConfiguration) {
      throw new Error(
        "ValidateOfferInput::isValidInput() must have an offerConfiguration to validate."
      );
    }
    return true;
  }
  /**
   * Function that constructs the input map object containing all query parameters for API
   * @returns {any}
   */
  public getInputMap(): any {
    const inputMap: any = {};

    if (this.context) {
      inputMap.context = this.context;
    }

    if (this.forceInvalidateCache) {
      inputMap.forceinvalidatecache = this.forceInvalidateCache;
    }

    if (this.isLoggedIn) {
      inputMap.isloggedin = this.isLoggedIn;
    }

    if (this.referencedatetime) {
      inputMap.referencedatetime = this.referencedatetime;
    }

    if (this.fields) {
      inputMap.fields = this.fields.join(",");
    }

    return inputMap;
  }

  /**
   * Function that constructs the API path for ApexRest
   * Handles scenerio when namespace is null
   * @returns {string}
   */
  public getAPIPath(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!this.catalogCode) {
      throw new Error(
        "ValidateOfferInput::getAPIPath() must have catalogCode."
      );
    }

    if (!this.offerCode) {
      throw new Error("ValidateOfferInput::getAPIPath() must have offerCode.");
    }

    return (
      `${SalesForceNamespace.restAPIPrefix}/v3/catalogs/${
        this.catalogCode
      }/offers/${this.offerCode}` + encodedQueryString
    );
  }

  /**
   * Function that constructs the API path for secure server
   * @returns {string}
   */
  public getSecureServerRestUrl(): string {
    if (!this.secureServerEndPointUrl) {
      this.secureServerEndPointUrl = "/validateoffer";
    }
    return (
      DigitalCommerceSDKConfig.secureServerUrl + this.secureServerEndPointUrl
    );
  }

  /**
   * Function that constructs the Anonymous API path for Rest
   * @returns {string}
   */
  public getAnonymousSiteRestUrl(): string {
    const encodedQueryString = this.getEncodedQueryString();

    if (!DigitalCommerceSDKConfig.anonymousSiteConfigUrl) {
      throw new Error(
        "ValidateOfferInput::getAnonymousRestUrl() must have anonymousSiteUrl."
      );
    }

    return (
      DigitalCommerceSDKConfig.anonymousSiteConfigUrl +
      `/v3/catalogs/${this.catalogCode}/offers/${this.offerCode}` +
      encodedQueryString
    );
  }

  /**
   * Function that constructs the Request body json for ApexRest
   * Handles scenerio when JSONResult, accountId and catalogCode is null
   * @returns {string}
   */
  public getRequestPayload(): any {
    if (!this.offerConfiguration) {
      throw new Error(
        "ValidateOfferInput::getRequestPayload() must have offerConfiguration."
      );
    }
    const requestBody = { result: this.offerConfiguration.result };
    return requestBody;
  }

  /**
   * Function that constructs the query string for API
   * @returns {string}
   */
  protected getEncodedQueryString(): string {
    const fieldsToInclude = [
      "context",
      "forceinvalidatecache",
      "fields",
      "isloggedin",
      "referencedatetime"
    ];

    const encodedQueryString = queryStringEncode(
      this.getInputMap(),
      true,
      fieldsToInclude
    );

    return encodedQueryString;
  }
}
